<template>
	<div id="app">
		<input v-model="demoId" placeholder="查询全部" />
		<button @click="selectDemo">查询</button>
		<table v-if="demoList.length > 0">
			<tr>
				<td>id</td>
				<td>姓名</td>
				<td>性别</td>
				<td>年龄</td>
				<td>住址</td>
				<td>操作</td>
			</tr>
			<tr v-for="demo in demoList">
				<td><input v-model="demo.id" /></td>
				<td>{{demo.name}}</td>
				<td>{{demo.gender == 1 ? '男' : '女'}}</td>
				<td>{{demo.age}}</td>
				<td>{{demo.address}}</td>
				<td>
					<button @click="deleteById(demo.id)">删除</button>
				</td>
			</tr>
			<tr>
				<td></td>
				<td><input /></td>
				<td><input /></td>
				<td><input /></td>
				<td><input /></td>
				<td><button>添加</button></td>
			</tr>
		</table>
	</div>
</template>

<script>
	import axios from 'axios'
	export default {
		name: 'databaseDemo',
		props: {
			demoId: {
				type: String,
				default: ''
			},
			demoList: {
				type: Array,
				default () {
					return []
				}
			}
		},
		methods: {
			selectDemo() {
				var that = this
				if (this.demoId == '') {
					axios.get('/demo/getAll')
						.then(function(result) {
							that.demoList = result.data
						}).catch(function(error) {
							console.log(error)
						})
				} else {
					axios.get('/demo/getById/' + that.demoId)
						.then(result => {
							var list = [result.data]
							that.demoList = list
						}).catch(error => {
							console.log(error)
						})
				}
			},
			deleteById(id) {
				var that = this
				axios.delete('http://localhost:8081/deleteById/' + id)
					.then(function(result) {
						if (result.data) {
							that.selectDemo()
							window.alert('删除成功')
						} else {
							window.alert('删除失败')
						}
					}).catch(function(error) {
						console.log(error)
					})
			}
		}
	}
</script>

